.keypad > div > div {
  margin: 5px;
  width: 40px;
  height: 50px;
  background: #eee;
  display: inline-block;
  border-radius: 6px;
  line-height: 50px;
}

.keypad > div > div.green {
  background: #5ac85a;
  color: #fff;
  transition: all 0.3s ease-in;
}
.keypad > div > div.yellow {
  background: #e2cc68;
  color: #fff;
  transition: all 0.3s ease-in;
}
.keypad > div > div.grey {
  background: #a1a1a1;
  color: #fff;
  transition: all 0.3s ease-in;
}

.modal {
  background: rgba(255,255,255,0.7);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.modal div {
  max-width: 480px;
  background: #fff;
  padding: 40px;
  border-radius: 10px;
  margin: 10% auto;
  box-shadow: 2px 2px 10px rgba(0,0,0,0.3);
}
.modal .solution {
  color: #ff004c;
  font-weight: bold;
  font-size: 0.8em;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* animation using keyframes */
@keyframes flip {
  /* starting state */
  0% {
    transform: rotateX(0);
    background: #fff;
    border-color: #333;
  }
  /* just before mid-way */
  45% {
    /* flat on the screen */
    transform: rotateX(90deg);
    background: #fff;
    border-color: #333;
  }
  /* just after mid-way */
  55% {
    transform: rotateX(90deg);
    /* background of this is the background variable */
    background: var(--background);
    border-color: var(border-color);
    color: #eee;
  }
  /* ending state */
  100% {
    transform: rotateX(0);
    background: var(--background);
    border-color: var(border-color);
    color: #eee;
  }
}

/* for when text is entered */
@keyframes bounce {
  0% {
    transform: scale(1);
    /* ddd is a slightly lighter grey */
    border-color: #ddd;
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
    border-color: #333;
  }
}

div.bounce{
  animation: keybounce 0.2s;
}

/* for when text is entered */
@keyframes keybounce {
  0% {
    transform: translate(0px, -1px) scale(1);
    /* ddd is a slightly lighter grey */
    border-color: #ddd;
  }
  45% {
    transform: translate(0px, -1.2px) scale(1);
    /* transform: scale(1.05); */
  }
  65% {
    /* transform: scale(1.1); */
  }
  100% {
    transform: translate(0px, 1.2px) scale(1);
    border-color: #333;
  }
}



@keyframes shake {
0%  { transform: translate(2px, 1px)   rotate(0deg);  }
10% { transform: translate(-1px, -2px) rotate(-2deg); }
20% { transform: translate(-3px, 0px)  rotate(3deg);  }
30% { transform: translate(0px, 2px)   rotate(0deg);  }
40% { transform: translate(1px, -1px)  rotate(1deg);  }
50% { transform: translate(-1px, 2px)  rotate(-1deg); }
60% { transform: translate(-3px, 1px)  rotate(0deg);  }
70% { transform: translate(2px, 1px)   rotate(-2deg); }
80% { transform: translate(-1px, -1px) rotate(4deg);  }
90% { transform: translate(2px, 2px)   rotate(0deg);  }
100%{ transform: translate(1px, -2px)  rotate(-1deg); }
}
